"use client";

import { useEffect, useState } from "react";
import { useRouter } from "next/navigation";
import api from "../../lib/axios";

export default function DashboardPage() {
  const [user, setUser] = useState<any>(null);
  const router = useRouter();

  useEffect(() => {
    const token = localStorage.getItem("token");
    if (!token) return router.push("/login");

    const fetchUser = async () => {
      try {
        const res = await api.get("/auth/me", {
          headers: { Authorization: `Bearer ${token}` },
        });
        setUser(res.data);
      } catch {
        router.push("/login");
      }
    };

    fetchUser();
  }, [router]);

  if (!user) return <p className="p-10 text-white">Loading dashboard...</p>;

  return (
    <div className="min-h-screen bg-gradient-to-b from-indigo-600 via-purple-600 to-pink-600 flex">
      
      {/* LEFT SIDEBAR */}
      <aside className="w-64 bg-white/10 backdrop-blur-md p-6 text-white border-r border-white/20">
        <h1 className="text-2xl font-bold mb-8">ðŸš€ Flock Admin</h1>

        <nav className="space-y-4">
          <button 
            onClick={() => router.push("/dashboard")}
            className="block w-full text-left px-4 py-2 rounded-lg bg-white/10 hover:bg-white/20 transition"
          >
            Dashboard
          </button>
        </nav>

        <div className="absolute bottom-6 left-6 w-[200px]">
          <button
            onClick={() => {
              localStorage.removeItem("token");
              router.push("/login");
            }}
            className="w-full bg-red-600 hover:bg-red-700 text-white py-2 rounded-xl transition"
          >
            Logout
          </button>
        </div>
      </aside>

      {/* MAIN CONTENT */}
      <main className="flex-1 p-10 text-white">
        
        {/* HEADER */}
        <h1 className="text-4xl font-bold">Welcome, {user.name} ðŸ‘‹</h1>
        <p className="opacity-80 mt-1">Role: {user.role}</p>

        {/* GRID CONTAINER */}
        <div className="mt-10 grid grid-cols-1 md:grid-cols-2 gap-8">

          {/* PROFILE CARD */}
          <div className="bg-white/10 p-6 rounded-2xl shadow-lg backdrop-blur-md border border-white/20">
            <h2 className="text-xl font-bold mb-2">ðŸ‘¤ Your Profile</h2>
            <p className="opacity-80">Email: {user.email}</p>
            <p className="opacity-80">Role: {user.role}</p>
          </div>

          {/* USER GENERAL FEATURE */}
          <div className="bg-white/10 p-6 rounded-2xl shadow-lg backdrop-blur-md border border-white/20">
            <h2 className="text-xl font-bold mb-2">âœ¨ General Feature</h2>
            <p className="opacity-80">Visible to all users.</p>
          </div>

          {/* ADMIN SECTION */}
          {user.role === "admin" ? (
            <div className="bg-green-600/20 p-6 rounded-2xl border border-green-400">
              <h2 className="text-xl font-bold">ðŸ›  Admin Panel</h2>
              <p className="opacity-80 mb-4">You have full admin access.</p>

              <div className="flex gap-4">
                <button
                  onClick={() => router.push("/admin/manage-users")}
                  className="bg-green-600 hover:bg-green-700 px-4 py-2 rounded-lg text-white"
                >
                  Manage Users
                </button>

                <button
                  onClick={() => router.push("/admin/settings")}
                  className="bg-green-700 hover:bg-green-800 px-4 py-2 rounded-lg text-white"
                >
                  Settings
                </button>
              </div>
            </div>
          ) : (
            <div className="bg-red-600/20 p-6 rounded-2xl border border-red-400">
              <h2 className="text-xl font-bold">ðŸ”’ Admin Only Feature</h2>
              <p className="opacity-70 mt-1">You do not have permission.</p>
            </div>
          )}

        </div>
      </main>
    </div>
  );
}
